.g-code-paper-wrapper {
    margin: 10px auto;
}
.g-code-paper-textarea {
    background-color: rgba(0, 0, 0, 0.05);
    border-color: rgba(0, 0, 0, 0.1);
    padding: 1em;
    border-radius: 0.25em;
    width: 100%;
    height: 200px;
    min-height: 48px;
    overflow: hidden;
    overflow-y: auto;
    resize: vertical;
}
