.g-image-preview-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-bottom: 0 !important;
    // z-index: 100;
}

.g-fixed {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #00000078;
    overflow: auto;
    z-index: 100; // WARN
}

.g-image-preview-icon-close {
    position: absolute;
    top: 10px;
    right: 20px;
    color: white;
    font-size: 26px;
    cursor: pointer;
    &:hover {
        color: rgba(241, 74, 102, 0.9);
        transition: 0.3s ease-in-out;
    }
}

.g-image-preview-image {
    cursor: move;
    position: absolute;
}

.g-image-preview-action-bar {
    position: absolute;
    bottom: 0px;
    width: 100%;
    height: 50px;
    display: flex;
    line-height: 50px;
    align-items: center;
    flex-flow: row nowrap;
    justify-content: center;
    & > i {
        color: white;
        font-size: 26px;
        cursor: pointer;
        margin-left: 30px;
        background: none;
        border: none;
        outline: none;
        &:hover {
            color: rgba(74, 236, 241, 0.9);
            transition: 0.3s ease-in-out;
        }
        &:first-child {
            margin: 0;
        }
    }
}
